body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  padding: 3px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: 1px solid red;
}


.line {
  display: inline-block;
  width: 1px;
  border: 4px blue;
  margin: 0;
  background-color: black;
  height: 100px;
  position: absolute;
  z-index: 3;
}

.selected {
  display: none;
  color: red;
  background-color: red;
  border-color: red;
}

.up {
  color: transparent;
  background-color: transparent;
  border-color: #ccc
}


.line label {
  margin: 5px;
  display: block;
  position: absolute;
  border-radius: 100%;
  font-family: monospace;
  text-align: center;
  padding: 2px;
  background-color: white;
}
.freq {
  top: -29px;
  left: -6px;
  -webkit-transform: rotate(-25deg);
          transform: rotate(-25deg);
}

.keyboard {
  top: 100px;
  left: -11px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.freqInput {
  border: 1px solid black;
  width: 400px;
  height: 100px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: 1px solid red;
}

.freqInput:hover {
  cursor: none;
}
.freqInput.full:hover {
  cursor: default;
}

.freqInput:hover .line.selected {
  display: inline-block;
}

section {
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: flex-end;
}

section .freqInput {
  margin-bottom: 20px;
}

.freqInput + button {
  margin: 5px;
}

body {
  display: flex;
  justify-content: center;
  margin: 10%;
  box-sizing: border-box;
}

